import {Component, OnInit, ElementRef} from '@angular/core';
import {ActivitiesService} from "./activities.service";
import {MomentPipe} from "../../utils/moment.pipe";
import {ActivitiesMessageComponent} from "./activities-message/activities-message.component";
import {ActivitiesTaskComponent} from "./activities-task/activities-task.component";
import {ActivitiesNotificationComponent} from "./activities-notification/activities-notification.component";

declare var $: any;

@Component({
  moduleId: module.id,
  selector: 'sa-activities',
  templateUrl: 'activities.component.html',
  directives: [ActivitiesMessageComponent, ActivitiesTaskComponent, ActivitiesNotificationComponent],
  providers: [ActivitiesService],
  pipes: [MomentPipe]
})
export class ActivitiesComponent implements OnInit {
  count:number;
  lastUpdate:any;
  active:boolean;
  activities:any;
  currentActivity: any;
  loading: boolean;

  constructor(private activitiesService:ActivitiesService, public el:ElementRef) {
    this.active = false;
    this.loading = false;
    this.activities = [];
    this.count = 0;
    this.lastUpdate = new Date();
  }

  ngOnInit() {
    this.activitiesService.getActivities().subscribe(data=> {
      this.activities = data;
      this.count = data.reduce((sum, it)=> sum + it.data.length, 0);
      this.currentActivity = data[0];
    });
  }

  setActivity(activity){
    this.currentActivity = activity;
  }

  onToggle() {
    let dropdown = $('.ajax-dropdown', this.el.nativeElement);
    this.active = !this.active;
    if (this.active) {
      dropdown.fadeIn()
    } else {
      dropdown.fadeOut()
    }
  }

  update(){
    this.loading= true;
    setTimeout(()=>{
      this.lastUpdate = new Date()
      this.loading = false
    }, 1000)
  }

}
